import { Component, Input, OnChanges} from '@angular/core';
import { ViewChild }				  from '@angular/core';
import { AngularFireDatabase }        from 'angularfire2/database';
import {BaseChartDirective}           from 'ng2-charts/ng2-charts';
import { Project }                    from './../class/project';
import { Chart }                      from './../class/chart';
import { User }                       from './../class/user';

@Component({
  selector: 'chart',
  templateUrl: './chart.component.html'
})

export class ChartComponent implements OnChanges {
	@ViewChild(BaseChartDirective) chart: BaseChartDirective;
	@Input() project: Project;
	chartUserId: Chart = new Chart();

	constructor(private database: AngularFireDatabase){}

	ngOnChanges(){
		if(this.project !== undefined){
			this.database.list('/global-stats/projects/' + this.project.id + '/users').subscribe(data => {
				let nbUser: number = 0;
	      		data.forEach(item => {
	        		this.cpyInformation(item, nbUser);
	        		nbUser ++;
	      		})
	      		this.drawChart();
	    	})
		}
	}

	cpyInformation(info: any, nbUser: number): void{
    	let newUser: User = new User();
    	newUser.create(nbUser, info.idCount, info.patientCount, info.userId);
    	this.project.users.push(newUser);
  	}

  	drawChart(): void{
	    let data: number[] = [];
	    let labels: string[] = [];
	    for(let i = 0; i < this.project.users.length; i++){
	      data.push(this.project.users[i].idCount);
	      labels.push(this.project.users[i].id);
	    }
	    this.chartUserId.labels = labels;
	    this.chartUserId.data = [{data: data, label: "Number of identification"}];
	    this.chartUserId.type = 'bar';
	    this.chartUserId.legend = true;
	    this.chartUserId.left = '200px';
	    this.chartUserId.top = (window.innerHeight * 16) / 100 + 'px';
	    this.chartUserId.width = '565px';
	    this.chartUserId.height = '300px';
	    this.chartUserId.options = {
	    responsive:true,
	    maintainAspectRatio: false,
	    scales: {
	    	yAxes: [{
	        	ticks: {
	        	beginAtZero: true,
	        	fontSize: 10
	        	}
	      	}],
	      	xAxes: [{
	        	ticks: {
	        		display: true,
	        		fontSize: 10
	        	}
	      	}]
	    },
	    legend: {
	    	labels: {
	    		fontColor: 'red'
	      	}
	    }
	  	};
	  	setTimeout(() => {
            if (this.chart && this.chart.chart && this.chart.chart.config) {
                this.chart.chart.config.data.labels = this.chartUserId.labels;
                this.chart.chart.update();
            }
        });
	}
}



