import { Component, OnInit }   from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Project }             from './../class/project';

@Component({
  selector: 'dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})

export class DashboardComponent implements OnInit{
	projects: Project[];
	selectedProject: Project;

	constructor(private database: AngularFireDatabase){}

	ngOnInit(){
		this.projects = [];
	    this.database.list('/global-stats/projects').subscribe(data => {
	    	data.forEach(item => {
	    		this.cpyInformation(item);
	    	})
	    });
  	}

  	cpyInformation(info): void{
  		let newProject: Project = new Project();
  		newProject.create(info.idCount, info.patientCount, info.projectId, 
  						  info.projectName);
  		this.projects.push(newProject);
  	}

  	selectProject(project: Project): void{
  		this.selectedProject = project;
  		this.selectedProject.users = [];
  	}
}



