import * as moment from 'moment';

export class Session{
	userId: string;
	hardwareVersion: string;
	startTime: string;
	latitude: string;
	longitude: string;

	constructor(){}

	cpyInformation(info): void{
		this.userId = info.userId;
		this.hardwareVersion = info.hardwareVersion;
		let date: Date;
		if(info.startTime === undefined){
			date = new Date(info.sessionStartTime);
		}
		else{
			date = new Date(info.startTime);
		}
		this.startTime = moment(date).format("DD/MM/YYYY::HH:mm");
		this.latitude = info.latitude;
		this.longitude = info.longitude;
	}
}


