import { Component } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { AngularFireAuth } from 'angularfire2/auth';
import { Router } from '@angular/router';
import * as firebase from 'firebase/app';

@Component({
  selector: 'app-root',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})

export class LoginComponent {
  token: string = "eyJhbGciOiJSUzI1NiIsInR5cCI6IkpXVCJ9..Kozx76n6NNSd1Dhsav0HH-Z1CQqTmfxgiXIYf_cvTb10IrrTmWGrY8fgUrm_Ew7T_TJxXsim07brsfMvF3Xa3_zOugZpk5Z2OqeQcAKsuf-WvBtakbF3mrN-JsdIb4cBJ0IW0N1IPk2N2bD0ZgLoeXaLnXxPT7Wgw7GWdIThpTyfNTVYOMc7UMKHJ4pkS-rltmPvcPIwQd9wZhEPEKq33K4pOumr6jhJLY7xuB6YbSCeIqPUVliuulPVFuMNiUGKE1sz7iHK9fr0Ba7IQUmZRdZLlqD-Vo5Meb2BPLE0xD94J9hRMtmFbJJg5vY8q3inkqhGWghzf16qxqfzCI4yuQ";
  constructor(public afAuth: AngularFireAuth, private router: Router){}

  login(){
  	this.afAuth.auth.signInWithPopup(new firebase.auth.GoogleAuthProvider()).then(data => this.checkUser());
  }

  checkUser(){
  	if(this.authorizeUser()){
  		this.afAuth.auth.signInWithCustomToken(this.token).then(data => this.router.navigate(['/dashboard']));
  	}
  	else{
  		alert("bad user");
  	}
  }

  authorizeUser(): boolean{
  	let regex = new RegExp(`^.+@simprints\.com`);
	return regex.test(this.afAuth.auth.currentUser.email);
  }
}



